import { LitElement, html, customElement, property, css } from "lit-element";
import "@material/mwc-icon-button";

const author = 'PixelQuasar';
const homepage = 'https://open-wc.org/';
const footerTemplate = html`
  <footer>Made with love by <a href="${homepage}">${author}</a></footer>
`;

@customElement('zeitkontrast-app')
class ZeitkontrastApp extends LitElement {
    @property ({ type: Array }) placeholder:string[];
    @property ({ type: String }) startTime:string = "08:00";
    @property ({ type: String }) endTime:string = "17:00";
    @property ({ type: Array }) breaks:string[] = [];

    constructor() {
        super();
        const currentTime = new Date();
        this.endTime = currentTime.toTimeString().slice(0, 5);
    }

    connectedCallback() {
        super.connectedCallback();
        console.log('app connected');
    }

    static get styles() {
        return css`

                :host {
                    font-family: Helvetica, "Helvetica Neue", Arial, sans-serif;
                    display:flex;
                    flex-direction: column;
                    align-items: center;
                    margin-top: 50px;
                }
                header{
                    display:flex;
                    flex-direction: column;
                    align-items: center;
                    font-family: monospace;
                }
                .container{
                    box-sizing: border-box;
                    margin-left: 20px;
                    margin-right: 20px;
                    padding-right: 8px;
                    padding-left: 8px;
                    max-width: 100%;
                }
                label {
                    font-size: 3rem;
                    width: 100px;
                }
                input {
                    border: unset;
                    font-size: 8rem;
                    max-width: max-content;
                    background-color: white;
                    color: #D0021B;
                    font-family: monospace;
                }
                .break-input{
                    font-size: 3rem;
                }
                .break-label{
                    width: 200px;
                    font-size: 2.5rem;
                }
                .pickers-container{
                    display: flex;
                    flex-direction: column;
                }
                .picker {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                }
                .break-container{
                    display:flex;
                    flex-direction: row;
                    align-items: center;
                    font-size: 1.5rem;
                }
                .work-result{
                    font-size: 2rem;
                }
                .worked-hours{
                    text-decoration: underline;
                }
                h2 {
                    margin-top: 70px;
                }
            `
    }

    render() {
        const decimalNetto = this._calculateNettoDecimal(this.startTime, this.endTime, this.breaks);
        const decimalBrutto = this._calculateBruttoDecimal(this.startTime, this.endTime);
        return html`
            <header class="container">
                <h1>time contrast</h1>
            </header>
            <main class="container">
            <div class="pickers-container">
                <div class="picker">
                    <label for="time-start">start time</label>
                    <input 
                        @change=${(e:Event) => this._changeStartTime(e)}
                        value=${this.startTime}
                        id="time-start" 
                        type="time" 
                        required>
                </div>
                
                <div class="picker">
                    <input 
                        @change=${(e:Event) => this._changeEndTime(e)}
                        value=${this.endTime}
                        id="time-end" 
                        type="time" 
                        required>
                    <label for="time-end">end time</label>
                </div>
            </div>

            <div class="picker">
                <mwc-icon-button
                icon="add"
                @click=${this._addBreak}
                ></mwc-icon-button>
                <label for="break" class="break-label">add break</label>
                <input class="break-input"
                    value="00:15"
                    id="break" 
                    type="time" 
                    required>

            </div>
            <p class="work-result">You worked for <span class="worked-hours">${this._convertDecimalToClock(decimalNetto)} hours</span>. (${decimalNetto.toFixed(2)} decimal)</p>
            <!-- <p>Duration (brutto, dec.): ${decimalBrutto.toFixed(2)} hours</p>
            <p>Duration (brutto): ${this._convertDecimalToClock(decimalBrutto)} hh:mm</p> -->
            ${this._renderBreaks(this.breaks)}
            </main>
            <!-- ${footerTemplate} -->
        `;
    }

    private _calculateBruttoDecimal(startTime:string, endTime: string){
        const startDate = this._convertTimeToDate(startTime);
        const endDate = this._convertTimeToDate(endTime);
        if (endDate <= startDate){
            endDate.setDate(endDate.getDate() + 1)
        }
        let hours = Math.abs(endDate.getTime() - startDate.getTime()) / 36e5;
        return hours;
    }

    private _calculateNettoDecimal(startTime:string, endTime: string, breaks: string[]){
        const startDate = this._convertTimeToDate(startTime);
        let endDate = this._convertTimeToDate(endTime);

        if (endDate <= startDate){
            endDate.setDate(endDate.getDate() + 1)
        }

        this.breaks.forEach(_break => {
            endDate = this._substractTimeFromDate(endDate, _break);
        })
        let hours = Math.abs(endDate.getTime() - startDate.getTime()) / 36e5;
        return hours;
    }

    private _convertDecimalToClock(decimalTime:number){
        if (decimalTime === 24){
            return "24:00"
        }
        const n = new Date(0,0);
        n.setMinutes(+decimalTime * 60); 
        const result = n.toTimeString().slice(0, 5);
        return result
    }

    private _convertTimeToDate(time: string){
        const date = new Date();
        date.setHours(parseInt(time.split(":")[0]));
        date.setMinutes(parseInt(time.split(":")[1]));
        date.setSeconds(0, 0);
        return date;
    }

    private _substractTimeFromDate(date: Date, time:string){
        date.setHours(date.getHours() - parseInt(time.split(":")[0]));
        date.setMinutes(date.getMinutes() - parseInt(time.split(":")[1]));
        return date;
    }

    private _changeStartTime(e:Event){
        const input = e.target as HTMLInputElement;
        this.startTime = input.value;
    }

    private _changeEndTime(e:Event){
        const input = e.target as HTMLInputElement;
        this.endTime = input.value;
    }

    private _addBreak(){
        const input = this.shadowRoot.querySelector('#break') as HTMLInputElement; 
        if (input.value !== "00:00"){
            this.breaks = [... this.breaks, input.value];
        }
    }

    private _renderBreaks(breaks:string[]){
        return html`
            ${breaks.length>0?html`
            <h2>breaks</h2>
            `:html``}
           ${breaks.map((_break, index) => html`
            <div class="break-container">
            <p>break ${index+1}: ${_break}</p>
            <mwc-icon-button icon="delete" @click=${() => this._deleteBreak(index)}></mwc-icon-button>
            </div>
            `)}
        `
    }

    private _deleteBreak(_index:number){
        this.breaks = this.breaks.filter((_break, index) => index !== _index)
    }   

}
